/* The navigation bar */
.navbar {
    overflow: hidden;
    background-color: #003d4d;
    position: fixed; /* Set the navbar to fixed position */
    top: 0; /* Position the navbar at the top of the page */
    width: 100%; /* Full width */
    display:flex;
  }
  
  /* Links inside the navbar */
  .navbar div {
    
    justify-content: center;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    width:33%;
    background: #003d4d;
  }
  
  /* Change background on mouse-over */
  .navbar a:hover {
    background: #ddd;
    color: black;
  }
  
  /* Main content */
  .main {
    margin-top: 30px; /* Add a top margin to avoid content overlay */
  }
  
  .columns{
    justify-content: center;
    padding:0 70px;
   margin-top:40px;
    display: block;
    margin-left:  auto;
    margin-right: auto;
    display:flex;
    flex-wrap: wrap;
    width:600px;
  }

 .column-test{
        background:#ffffff; 
        border:solid 2px #006680;
        border-radius: 10px;
        height: 120px;
        width: 120px;
        margin:5px;
        overflow: hidden;
        box-shadow: 0 3px 6px #999, 0 3px 6px #999;
 
 }

 .column-test:hover{
            height: 140px;
            width: 140px;
            -webkit-animation:ease-in;
                    animation:ease-in;
            -webkit-transition-duration: .5s;
                    transition-duration: .5s;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
           
  }

  .column-test img:hover{
                height: 140px;
